.spinner{
  padding-top: 100%;
  position: relative;
}
.spinner-wrp{
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  transform: translateY(50%);
  max-width: 1000px;
  margin: auto;
  /* margin-top: 10px; */
}
.spinner-inner{
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
}
.spinwrp{
  position: relative;
  padding: 10px;
  width: 100%;
  height: 100%;
}

.spinchart{
  position: relative;
  z-index: 7;
  background: rgb(255 255 255 / 5%);
  border-radius: 50%;
  width: 100%;
  height: 100%;

}

.spinchart canvas{
  /* transform: translateY(-25%) !important; */
}

.spinchart.spin{
  -webkit-transition: cubic-bezier(0.19, 1, 0.22, 1) 10s;
  transition: cubic-bezier(0.19, 1, 0.22, 1) 10s;
}

.spinarrow{
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;

  border: 4px solid #fff;
  border-radius: 50%;
  z-index: 7;
}

.spinwrp:after{
  content: "";
  width: 60px;
  height: 60px;
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  top: -10px;
  z-index: 9;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url(/src/assets/img/spinner-arrow.svg);
}


.spincontent{
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 99;
  margin: auto;
  transform: translateY(-33%);
}
.spincontent-wrp{
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: #271b40;
  border-radius: 50%;
  width: 73%;
  height: 73%;
  margin: auto;
  border: 3px solid #413657;
  z-index: 8;
}
.spincontent .inner{
  z-index: 9;
}


.spincontent .win-wrp{
  position: relative;
  width: 162px;
  height: 162px;
  margin: auto;
}
.spincontent .win-wrp:after{
  content: "";
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 162px;
  height: 162px;
  position: absolute;
  background-image: url(/src/assets/img/win.svg);
}

.spincontent .win-wrp img{
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  width: 110px;
  height: 110px;
  object-fit: cover;
  margin: auto;
  border-radius: 50%;
}


