.history-filter .item{
  background-color: #271B40;
  color: #735893;
  text-transform: uppercase;
  border-radius: 12px;
  height: 38px;
  padding: 0 15px;
  display: flex;
  align-items: center;
  margin-right: 7px;
  font-weight: bold;
  cursor: pointer;
}
.history-filter .item:last-child{
  margin-right: 0;
}
.history-filter .item.active{
  background-color: #452F5E;
  color: #FFF;
}
.history-filter .item svg path{
  fill: #735893;
}
.history-filter .item.active svg path{
  fill: #FFF;
}
.history-filter .item svg{
  margin-right: 5px;
}







