.ant-menu-inline-collapsed-tooltip { display: none; }

.leadbox{
  background-color: #1C0D34;
  border-radius: 24px;
  padding: 16px 30px;
  border: 1px solid #735893;
  position: relative;
  margin-bottom: 15px;
}

.leadbox:nth-child(1):after, .leadbox:nth-child(2):after, .leadbox:nth-child(3):after{
  content: "";
  position: absolute;
  width: 24px;
  height: 24px;
  left: -12px;
  background-size: contain;
  background-repeat: no-repeat;
  background-image: url(/src/assets/img/stats-icon-1.svg); 
}
.leadbox:nth-child(2):after{
  background-image: url(/src/assets/img/stats-icon-2.svg); 
}
.leadbox:nth-child(3):after{
  background-image: url(/src/assets/img/stats-icon-3.svg); 
}
.leadbox:nth-child(1){
  background: radial-gradient(circle at 100% 100%, #1c0d34 0, #1c0d34 22px, transparent 22px) 0% 0%/24px 24px no-repeat,
  radial-gradient(circle at 0 100%, #1c0d34 0, #1c0d34 22px, transparent 22px) 100% 0%/24px 24px no-repeat,
  radial-gradient(circle at 100% 0, #1c0d34 0, #1c0d34 22px, transparent 22px) 0% 100%/24px 24px no-repeat,
  radial-gradient(circle at 0 0, #1c0d34 0, #1c0d34 22px, transparent 22px) 100% 100%/24px 24px no-repeat,
  linear-gradient(#1c0d34, #1c0d34) 50% 50%/calc(100% - 4px) calc(100% - 48px) no-repeat,
  linear-gradient(#1c0d34, #1c0d34) 50% 50%/calc(100% - 48px) calc(100% - 4px) no-repeat,
  linear-gradient(90deg, #fddd33 0%, rgba(254,141,21,0) 100%);  
  box-sizing: border-box;
  border: none;
}

.leadbox:nth-child(2){
  background: radial-gradient(circle at 100% 100%, #1c0d34 0, #1c0d34 22px, transparent 22px) 0% 0%/24px 24px no-repeat,
  radial-gradient(circle at 0 100%, #1c0d34 0, #1c0d34 22px, transparent 22px) 100% 0%/24px 24px no-repeat,
  radial-gradient(circle at 100% 0, #1c0d34 0, #1c0d34 22px, transparent 22px) 0% 100%/24px 24px no-repeat,
  radial-gradient(circle at 0 0, #1c0d34 0, #1c0d34 22px, transparent 22px) 100% 100%/24px 24px no-repeat,
  linear-gradient(#1c0d34, #1c0d34) 50% 50%/calc(100% - 4px) calc(100% - 48px) no-repeat,
  linear-gradient(#1c0d34, #1c0d34) 50% 50%/calc(100% - 48px) calc(100% - 4px) no-repeat,
  linear-gradient(90deg, #b2b2b2 0%, rgba(254,141,21,0) 100%);
  box-sizing: border-box;
  border: none;
}

.leadbox:nth-child(3){
  background: radial-gradient(circle at 100% 100%, #1c0d34 0, #1c0d34 22px, transparent 22px) 0% 0%/24px 24px no-repeat,
  radial-gradient(circle at 0 100%, #1c0d34 0, #1c0d34 22px, transparent 22px) 100% 0%/24px 24px no-repeat,
  radial-gradient(circle at 100% 0, #1c0d34 0, #1c0d34 22px, transparent 22px) 0% 100%/24px 24px no-repeat,
  radial-gradient(circle at 0 0, #1c0d34 0, #1c0d34 22px, transparent 22px) 100% 100%/24px 24px no-repeat,
  linear-gradient(#1c0d34, #1c0d34) 50% 50%/calc(100% - 4px) calc(100% - 48px) no-repeat,
  linear-gradient(#1c0d34, #1c0d34) 50% 50%/calc(100% - 48px) calc(100% - 4px) no-repeat,
  linear-gradient(90deg, #A16511 0%, rgba(254,141,21,0) 100%);
  box-sizing: border-box;
  border: none;
}



.box-deposit{
  display: none;
}
.box-deposit.show{
  display: block;
}

.blink_me {
  animation: blinker 2s linear infinite;
}
@keyframes blinker {
  from { opacity: 1.0; }
  50% { opacity: 0.5; }
  to { opacity: 1.0; }
}

.ant-pagination-options{
  display: none !important;
}
.ant-table-expanded-row-fixed{
  width: 100% !important;
}

