@media screen and (max-width: 1023px) {
  .ant-layout .ant-layout-header{
    background-color:rgba(28, 13, 52, 0);
    backdrop-filter: blur(15px);
  }
  .leadbox{
    padding: 10px 26px;
    margin-bottom: 10rgba(28, 13, 52, 0.212);
    margin-bottom: 5px;
    margin-left: 10px;
  }

  .claimbox, .statsbox{
    border-radius: 18px;
  }

  .box-deposit{
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 999;
    border-bottom-left-radius: 0 !important;
    border-bottom-right-radius: 0 !important;
  }

  .box-deposit-mask{
    content: "";
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    z-index: 999;
    backdrop-filter: blur(8px);
  }
}

